import React from 'react';
import './CSS/Body.css';
import img1 from './images/img1.png';
import img2 from './images/img2.jpg';
import img3 from './images/img3.jpg';
import img4 from './images/img4.jpg';
import img5 from './images/img5.jpg';
import img6 from './images/img6.jpg';
import img7 from './images/img7.jpg';
import img8 from './images/img8.jpg';

class Body extends React.Component{
    constructor(props){
        super(props);
        let imageList = [img1,img2,img3,img4,img5,img6,img7,img8]
        this.state = {
            imageList : imageList,
            listSize : Math.ceil(imageList.length / 4)
        }
    }

    renderPhotoList = ()=>{
        let photoList = this.state.imageList;
        let list = photoList.map((item, index)=>
            <li key={index.toString()}>
                <img src={item} alt="stranger things cap"/>
            </li>
        );
        return list;
    }

    movePhotosTo = (id)=>{
        
    }

    render(){
        return(
            <section>
                <div className="storyLine">
                    <h1>Storyline</h1>
                    <p>In a small town where everyone knows everyone,
                        a peculiar incident starts a chain of events that leads 
                        to the disappearance of a child, which begins to tear at the
                        fabric of an otherwise peaceful community. Dark government agencies and
                        seemingly malevolent supernatural forces converge on the town, while a few
                        locals begins to understand that there's more going on than meets the eye.
                    </p>
                    <ul>
                        <li>Plot Summary</li>
                        <li>Plot Synopsis</li>
                    </ul>
                    <ul>
                        <li>Plot keywords: <strong>government conspiracy | mk ultra | cover up | monster | 
                            1980s | etc</strong>
                        </li>
                        <li>Taglines: <strong>It only gets stranger</strong></li>
                        <li>Genres: <strong>Drama | Fantasy | Horror | Mistery | Sci-Fi | Thriller</strong></li>
                        <li>Certificates: <strong>M</strong></li>
                        <li>Parents guide: <strong>view content advisory</strong></li>
                    </ul>
                </div>
                <div className="photos">
                    <h1>Photos</h1>
                    <ul>
                        {this.renderPhotoList()}
                    </ul>
                </div>
            </section>
        );
    }
}

export default Body;