import React from 'react';
import './CSS/Header.css';
import Logo from './images/imdb.png';
import Play from './images/play-button.png';
import Add from './images/add.png';
import Trending from './images/arrow.png';
import Exit from './images/exit.png'
const blackBGC1 = {
    background : 'rgba(70,70,70,0.7)'
};
const blackBGC2 = {
    background : 'rgba(50,50,50,0.6)'
};

class Header extends React.Component {
    constructor(props){
        super(props);

        this.state = {
            isTrailerOpen : {
                display: 'none'
            }
        }
    }
    playTrailer = ()=>{
        if(this.state.isTrailerOpen.display === 'none'){
            this.setState({isTrailerOpen : {display : 'grid'}});
        }else{
            this.setState({isTrailerOpen : {display : 'none'}});
        }
    }

    render(){
        return (
            <header>
                <div style={this.state.isTrailerOpen} className="youtubeTrailer">
                    <img onClick={this.playTrailer} className="img32 hover" src={Exit} alt="exit button"/>
                    <iframe 
                    title="stranger things trailer" 
                    src="https://www.youtube.com/embed/uHZ5JskXw4I" 
                    frameBorder="0" 
                    allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" 
                    allowFullScreen>
                    </iframe>
                </div>
                <div className="headerBasicInfo">
                    <img src={Logo} alt="Imdb logo" className="img64"/>
                    <div>
                        <p>Stranger Things</p>
                        <ul>
                            <li>M</li>
                            <li>51min</li>
                            <li>Drama, Fantasy, Horror</li>
                            <li>TV Series (2016)</li>
                        </ul>
                    </div>
                </div>
                <div className="headerNavBar" style={blackBGC1}>
                    <ul>
                        <li className="hover">Movie</li>
                        <li className="hover">News</li>
                        <li className="hover">Watchlist</li>
                        <li className="hover">Search</li>
                    </ul>
                </div>
                <div className="headerWatchTrailer">
                    <div onClick={this.playTrailer} className="button">
                        <img src={Play} alt="Play Stranger Things Trailer"/>
                        <p>Watch trailer</p>
                    </div>
                </div>
                <div className="headerInfo">
                    <div className="episodeGuide" style={blackBGC1}>
                        <div>
                            <p>8.9<span>/10</span></p>
                        </div>
                        <form action="/">
                            <p>Episode Guide</p>
                            <select name="episode_guide">
                                <option>n options</option>
                            </select>
                        </form>
                    </div>
                    <div className="serieInfo" style={blackBGC1}>
                        <p>When a young boy disappears, his friends mother, a police chief,
                            and his friends must confront terrifying forces in order to get
                            him back.
                        </p>
                        <p>Creators: <strong>Matt Duffer, Ross Duffer</strong></p>
                        <p>Stars: <strong>Millie Bobby, Finn Wolfhard, Winona Ryder</strong></p>
                    </div>
                    <div className="addToWatchlist"style={blackBGC2}>
                        <div className="button">
                            <img src={Add} alt="add to watchlists button"/>
                            <p>Add to watchlists</p>
                        </div>
                        <div>
                            <p>Reviews <br/> <span>925 user | 168 critic</span></p>
                        </div>
                        <div>
                            <img className="img32" src={Trending} alt="Trending"/>
                            <p>Popularity <br/> <span>42(+7)</span></p>
                        </div>
                    </div>
                </div>
            </header>
        );
    }
    
}

export default Header;
